<template>
  <div class="all">

    <el-card
      v-if="isSentense==false">

      <div class="value">
        <p id="word">
          {{wordInfo.word}}
        </p>
      </div>
      <div class="announce">
        英式读音
        {{wordInfo.uk_phonetic}}
        <el-button
          type="primary" plain
          icon="el-icon-service"
          size="mini" ref="uk"
          @click="ukSpeech">
        </el-button>
        <br>
        美式读音
        {{wordInfo.us_phonetic}}
        <el-button
          type="primary" plain
          icon="el-icon-service"
          size="mini" ref="us"
          @click="usSpeech">
        </el-button>

      </div>
      <h3>释义</h3>
      <div class="explains">
        <ul
          v-for="(exp,index) in wordInfo.explains"
          :key="index">
          <li>
            {{exp}}
          </li>
        </ul>

      </div>

      <div
        v-if="wordInfo.wordGroup.length">
        <h3>词组</h3>
        <ul
          v-for="(exp,index) in wordInfo.wordGroup"
          :key="index">
          <li>
            {{exp}}
          </li>
        </ul>

      </div>
      <div v-if="!this.flag">
        <h3>网络释义及词组</h3>
        <ul
          v-for="(exp,index) in wordInfo.web"
          :key="index">
          <li>
            {{exp.key}}
            {{exp.value}}
          </li>
        </ul>

      </div>

      <div
        v-if="wordInfo.sentences.length">
        <h3>双语例句</h3>
        <ul
          v-for="(exp,index) in wordInfo.sentences"
          :key="index">
          <li>
            {{exp.en}}
            {{exp.cn}}
          </li>
        </ul>
      </div>

    </el-card>

    <el-card
      v-if="isSentense">
      <h2>句子</h2>
      <h4>{{sentence.query}}
      </h4>

      <br>
      <h2>释义</h2>
      <h4>
        {{sentence.translation}}
      </h4>
      <el-button
        type="primary" plain
        icon="el-icon-service"
        size="mini" ref="uk"
        @click="sentenseSpeech">
      </el-button>
    </el-card>
    <p id="index">{{n+1}}/{{wordList.length}}</p>
    <el-button type="primary"
      plain id="btn1"
      @click="previous">上一个
    </el-button>
    <!-- <el-button type="danger"
      plain id="btn3">标记不会
    </el-button> -->
    <el-button type="success"
      plain id="btn2"
      @click="next">下一个
    </el-button>
  </div>
</template>
<script>
import axios from 'axios'


//代表能否在后台直接查询到单词信息的flag

export default {
  name: 'MyWords',

  data() {
    return {
      currentWord: '',
      wordList: [],
      wordInfo: {},
      phoneticArr: [],
      basic: {},
      arr: [],
      keyArr: [],
      flag: true,
      isSentense: false,
      sentence: {},
      n:0
    }
  },
  methods: {
    open2(msg) {
      this.$message({
        showClose: true,
        message: msg,
        type: 'success',
      })
    },
    getWordInfo(targetWord) {
      axios({
        method: 'post',
        url: 'http://121.40.113.226:7777/word/getWordInfo',
        data: this.$qs.stringify({
          word: targetWord,
        }),
      }).then((res) => {
        if (res.data.status == 400) {
          //词库里面没有的话就调用有道的接口去翻译

          this.getFromYouDao(this.wordList[this.n])
          this.wordInfo.sentences = []
          this.wordInfo.wordGroup = []

          this.flag = false
        } else {
          this.isSentense = false
          this.flag = true
          console.log(res)
          this.wordInfo = res.data.data
        }
      })
    },
    next() {
      if (this.n < this.wordList.length - 1) {
        this.n++
        this.getWordInfo(this.wordList[this.n])
      } else {
        this.open2('已经是最后一个单词了,今日共背了' + this.wordList.length + '个')
      }
    },
    previous() {
      if (this.n == 0) {
        this.open2('已经到头了')
      } else {
        if (this.n > 0) {
          this.n--
          if (this.n != -1) {
            this.getWordInfo(this.wordList[this.n])
          }
        }
      }
    },
    ukSpeech() {
      if (this.flag) {
        this.FileukSpeech()
      } else {
        this.YDukSpeech()
      }
    },
    usSpeech() {
      if (this.flag) {
        this.FileusSpeech()
      } else {
        this.YDusSpeech()
      }
    },

    FileukSpeech() {
      let audio = new Audio()

      audio.src = 'http://121.40.113.226/audio/' + this.wordInfo.word + '_uk.mp3'
      audio.play()
    },
    FileusSpeech() {
      let audio = new Audio()

      audio.src = 'http://121.40.113.226/audio/' + this.wordInfo.word + '_us.mp3'
      audio.play()
    },

    getFromYouDao(word) {
      let key = 'mtKD1CVzlGrj2rcSxK4DzTPz9m4zRscM'
      let appKey = '50649feb1569418e'
      let salt = new Date().getTime()
      let curtime = Date.parse(new Date()) / 1000
      axios({
        method: 'post',
        url: 'http://121.40.113.226:7777/api/',
        // url: 'api',
        headers: { 'content-type': 'application/x-www-form-urlencoded' },
        data: this.$qs.stringify({
          q: word,
          from: 'en',
          to: 'zh-CHS',
          salt: salt,
          curtime: curtime,
          appKey: appKey,
          sign: this.$sha256(appKey + this.truncate(word) + salt + curtime + key),
          signType: 'v3',
          ext: 'mp3',
          voice: '0',
          strict: 'true',
        }),
      }).then((res) => {
        console.log(res.data)
        //修改显示的卡片
        if (res.data.isWord == false) {
          this.isSentense = true
          this.sentence.query = res.data.query

          this.sentence.translation = res.data.translation
          this.sentence.speakUrl = res.data.speakUrl
        }

        // console.log(res)
        this.basic = res.data.basic
        this.arr = Object.values(this.basic)
        this.keyArr = Object.keys(this.basic)
        //设置读音数组
        let i = 0
        for (i; i < this.keyArr.length; i++) {
          if (this.keyArr[i] == 'uk-phonetic') break
        }
        this.phoneticArr[0] = this.arr[i]

        let j = 0
        for (j; j < this.keyArr.length; j++) {
          if (this.keyArr[j] == 'us-phonetic') break
        }
        this.phoneticArr[1] = this.arr[j]

        console.log(this.phoneticArr)
        //替换有道接口的结果到wordInfo对应的字段
        this.wordInfo.uk_phonetic = this.phoneticArr[0]
        this.wordInfo.us_phonetic = this.phoneticArr[1]
        this.wordInfo.sentences = []
        this.wordInfo.word = word
        this.wordInfo.explains = this.basic.explains
        this.wordInfo.wordGroup = []
        this.wordInfo.web = res.data.web
      })
    },
    truncate(q) {
      let len = q.length
      if (len <= 20) return q
      return q.substring(0, 10) + len + q.substring(len - 10, len)
    },

    YDukSpeech() {
      let audio = new Audio()
      //这里由于vue对变量名中含有-死活没法操作,只能强行将对象转换为数组之后再取值了
      let i = 0
      for (i; i < this.keyArr.length; i++) {
        if (this.keyArr[i] == 'uk-speech') break
      }
      audio.src = this.arr[i]
      audio.play()
      // console.log(this.arr[i])
    },
    YDusSpeech() {
      let audio = new Audio()
      //这里由于vue对变量名中含有-死活没法操作,只能强行将对象转换为数组之后再取值了
      let j = 0
      for (j; j < this.keyArr.length; j++) {
        if (this.keyArr[j] == 'us-speech') break
      }
      audio.src = this.arr[j]
      audio.play()
      // console.log(this.arr[j])
    },
    finished(num) {
      alert('今天背了' + num + '条')
    },

    sentenseSpeech() {
      let audio = new Audio()
      audio.src = this.sentence.speakUrl
      audio.play()
    },
  },

  mounted() {
    if (
      this.$store.state.uid == null ||
      this.$store.state.uid == '' ||
      this.$store.state.uid === undefined
    ) {
      alert('请先登录')
      this.$router.push('/login')
    }
    axios({
      method: 'post',
      url: 'http://121.40.113.226:7777/word/todayWords',
      data: {
        uid: this.$store.state.uid,
      },
    }).then((res) => {
      this.n = 0
      console.log(res)
      // this.wordList = res.data.data
      res.data.data.forEach((element) => {
        this.wordList.push(element.value)
      })
      this.getWordInfo(this.wordList[0])
      if (this.wordList.length == 0) {
        alert(
          '今天还没有单词要背哦,先去添加单词吧,添加完一般第二天才可以获取,记得今天先背添加的单词哦'
        )
      }
    })
  },
}
</script>
<style  scoped>
#btn1{
  position: fixed;
  left: 5px;
  bottom: 100px;
}
#btn2 {
  position: fixed;
  right: 5px;
  bottom: 100px;
}

.value {
  margin-top: -5vh;
  height: 7vh;
  font-size: 2em;
}

.announce {
  height: 5vh;
}
ul li {
  list-style: none;
}
.card {
  overflow-y: auto; /* 开启滚动显示溢出内容 */
}
/* 下面的单词页标 */
#index {
  margin-left: 20vh;

}
</style>
